import {Contract} from '../../eos/contract';
import {EOS} from '../../eos/eos';
import {GlobalTable} from '@/modules/contract/house/globalTable';
import {TokenTable} from '@/modules/contract/house/tokenTable';
import {Asset} from '@/modules/eos/asset';
import {TransactionReceipt} from '@/modules/eos/transactionReceipt';
import {Constants} from '@/modules/eos/constants';
import {Network} from '@/modules/eos/network';
import {BlackjackHistoryTable} from '@/modules/contract/blackjack/blackjackHistoryTable';
import {BlackjackGameTable} from '@/modules/contract/blackjack/blackjackGameTable';


enum PlayerAction {
    New, Hit, Double, Insure, Stand, Surrender,
}

enum Globals {
    Active = 101,
}

export class Blackjack extends Contract {
    public static readonly CONTRACT = Constants.NETWORK === Network.LOCALHOST ? 'blackjack' : 'godappblackj';

    public readonly globalVarTable: GlobalTable;
    public readonly tokensTable: TokenTable;
    public readonly gameTable: BlackjackGameTable;
    public readonly historyTable: BlackjackHistoryTable;

    public constructor(eos: EOS) {
        super(Blackjack.CONTRACT, eos);

        this.globalVarTable = new GlobalTable(eos, this.name);
        this.tokensTable = new TokenTable(eos, this.name);
        this.historyTable = new BlackjackHistoryTable(eos, this.name);
        this.gameTable = new BlackjackGameTable(eos, this.name);
    }

    public init(): Promise<TransactionReceipt> {
        return this.pushAction('init', {});
    }

    public setGlobal(key: number, value: number): Promise<TransactionReceipt> {
        return this.pushAction('setglobal', {key, value});
    }

    public setActive(active: boolean): Promise<TransactionReceipt> {
        return this.setGlobal(Globals.Active, active ? 1 : 0);
    }

    public play(asset: Asset): Promise<TransactionReceipt> {
        return this.transfer(asset, 'new');
    }

    public hit(): Promise<TransactionReceipt> {
        return this.playerAction(PlayerAction.Hit);
    }

    public stand(): Promise<TransactionReceipt> {
        return this.playerAction(PlayerAction.Stand);
    }

    public surrender(): Promise<TransactionReceipt> {
        return this.playerAction(PlayerAction.Surrender);
    }

    public hardClose(id: number, reason: string): Promise<TransactionReceipt> {
        return this.pushAction('hardClose', {game_id: id, reason});
    }

    public close(id: number): Promise<TransactionReceipt> {
        return this.pushAction('close', {game_id: id});
    }

    private playerAction(action: PlayerAction): Promise<TransactionReceipt> {
        return this.pushAction('playeraction', {player: this.accountName(), action});
    }

    // ACTION cleargames(uint32_t num);
}
